

.card {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    width: 400px;
    height: 330px;
    color: white;  
    box-shadow: 2px 2px 22px 4px #000000;
    border-radius: 20px;
    cursor: pointer;
}

.card:hover {
    transform: scale(1.05) rotate(2deg);
}



.pokeName{
    font-family:'Pokemon Hollow', sans-serif ;
}

.img-header {
    background-color: white;
    border-radius: 10px 10px 0 0 ;
    height: 500px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.img-pokemon{
    width: 110px;
    background-color: white;
}

.type{
    font-size: 12px;
}

.types-poke{
    display: flex;
    justify-content: space-around;
    width: 100%;
}

.div-gray{
    background: rgb(98,98,98);
    background: linear-gradient(90deg, rgba(98,98,98,1) 28%, rgba(144,144,144,1) 100%);
    padding:0.4rem 2rem;
    border-radius: 10px;
}



.footer-id {
    color: darkgray;
    margin-bottom: 10px;
    background-color: white;
    width: 30px;
    height: 30px;
    align-items: center;
    display: flex;
    justify-content: center;
    border-radius: 100%;
    color: black;
    font-weight: bold;
    font-size: 18px;
    margin-top: 1rem;
}

